
import { ClassProto } from '../class-proto';

const Tweenie = {
  foo: 1,
  run () {
      console.log(this);
    }
}

Tween.__proto__ = ClassProto;

const a = Object.create(Tweenie);

console.log('a', a.foo);
a.run();

export { Tweenie as Tweenie };


